.vertical-bar {
    float: left;
    width: 4vw;
    height: 100vh;
    background-color: #f9fafb;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
}

.top {
    padding-top: 5vh;
    height: 20vh;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
}

.bottom {
    padding-bottom: 5vh;
    height: 20vh;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
}


.icon {
    height: 1vw;
    width: 1vw;
}

.logo {
    border-bottom: 1px solid grey;
    padding-bottom: 2vh;
}

.container {
    padding-left: 15vw;
    padding-right: 15vw;
    padding-top: 5vh
}

.logo-name {
    font-size: 10vh;
    font-weight: bold;
    color: #3877ea;
}

.top-line {
    font-size: x-large;
    font-weight: 300;
}

.text {
    padding-bottom: 5vh;
}

.search-bar {
    background-image: url("https://www.w3schools.com/css/searchicon.png");
    background-position: 15px 15px;
    padding-left: 50px;
    background-repeat: no-repeat;
    width: 100%;
    height: 5vh;
    border: 1px solid #3877ea;
    border-radius: 3px;
    margin-bottom: 5vh;
}

.filter {
    width: 15%;
    height: 5vh;
    border: 1px solid black;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-radius: 3px;
    margin-bottom: 5vh;
}

.placeholder {
    opacity: 50%;
}

.bottom-container {
    height: 40vh;
    padding-left: 11vw;
    padding-right: 11vh;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, auto);
    gap: 5vw
    
}

.course  {
    height: 20vh;
    border-radius: 10px;
    box-shadow: 5px 10px #f4f4f4;
}

.top-line {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding-right: 10%;
}

.top-line h3 {
    padding-left: 5%;
}

.course p, .course .bubbles {
    padding-left: 5%;
}

.bubbles {
    display: flex;
    flex-direction: row;
    gap: 10px;
}

.bubble {
    background-color: #d2eaf5;
    border-radius: 10px;
    width: 15%;
}

.stars .icon {
    color: #b08bdf    ;
}